	/* Spinner */

	.lds-hourglass {
		display: inline-block;
		position: relative;
		width: 64px;
		height: 64px;
	  }
	  .lds-hourglass:after {
		content: " ";
		display: block;
		border-radius: 50%;
		width: 0;
		height: 0;
		margin: 6px;
		box-sizing: border-box;
		border: 26px solid #fff;
		border-color: #fff transparent #fff transparent;
		animation: lds-hourglass 1.2s infinite;
	  }
	  @keyframes lds-hourglass {
		0% {
		  transform: rotate(0);
		  animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
		}
		50% {
		  transform: rotate(900deg);
		  animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
		}
		100% {
		  transform: rotate(1800deg);
		}
	  }
	  


	  /* Details summary  */


	  summary {
		padding: 1rem 2em 1rem 0;
		display: block;
		position: relative;
	  }
	  summary::before, summary::after {
		width: 0.75em;
		height: 2px;
		position: absolute;
		top: 50%;
		right: 0;
		content: '';
		background-color: currentColor;
		text-align: right;
		transform: translateY(-50%);
		transition: transform 0.2s ease-in-out;
	  }
	  summary::after {
		transform: translateY(-50%) rotate(90deg);
	  }
	  [open] summary::after {
		transform: translateY(-50%) rotate(180deg);
	  }


	  /*Ripple, loading screen */

	  @keyframes lds-ripple {
		0% {
		  top: 96px;
		  left: 96px;
		  width: 0;
		  height: 0;
		  opacity: 1;
		}
		100% {
		  top: 18px;
		  left: 18px;
		  width: 156px;
		  height: 156px;
		  opacity: 0;
		}
	  }
	  @-webkit-keyframes lds-ripple {
		0% {
		  top: 96px;
		  left: 96px;
		  width: 0;
		  height: 0;
		  opacity: 1;
		}
		100% {
		  top: 18px;
		  left: 18px;
		  width: 156px;
		  height: 156px;
		  opacity: 0;
		}
	  }
	  .lds-ripple {
		position: relative;
	  }
	  .lds-ripple div {
		box-sizing: content-box;
		position: absolute;
		border-width: 4px;
		border-style: solid;
		opacity: 1;
		border-radius: 50%;
		-webkit-animation: lds-ripple 1.7s cubic-bezier(0, 0.2, 0.8, 1) infinite;
		animation: lds-ripple 1.7s cubic-bezier(0, 0.2, 0.8, 1) infinite;
	  }
	  .lds-ripple div:nth-child(1) {
		border-color: #93dbe9;
	  }
	  .lds-ripple div:nth-child(2) {
		border-color: #689cc5;
		-webkit-animation-delay: -0.85s;
		animation-delay: -0.85s;
	  }
	  .lds-ripple {
		width: 190px !important;
		height: 190px !important;
		-webkit-transform: translate(-95px, -95px) scale(0.95) translate(95px, 95px);
		transform: translate(-95px, -95px) scale(0.95) translate(95px, 95px);
	  }